<template>
<div style="height:100%;">
<div class="main-container">
    <div class="main-div">
    <div class="main-content">
            <!-- 分类选择 -->
            <el-cascader class="cate-select for-holder-color select-c" :options="options" size="small" placeholder="全部分类" change-on-select>
            </el-cascader>
            <!-- 状态选择 -->
            <el-cascader class="status-select for-holder-color select-c" :options="options" size="small" placeholder="全部状态" change-on-select>
            </el-cascader>
            <!-- 搜索框 -->
            <el-input placeholder="请输入名称/编码/规格" class="input-search" size="small">
            <el-button slot="append" icon="el-icon-search" size="mini"></el-button>
            </el-input>

            <!-- 高级搜索 -->
            <p class="high-search">高级搜索</p>
            <!-- 新增 -->
            <!-- <el-button size="medium" class="btn-add" plain>&nbsp;新增&nbsp;</el-button> -->
            <!-- 导出 -->
            <el-select placeholder="导出" v-model="value" class="select-out for-holder-color" size="medium"> <el-option
                  v-for="item in options2"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select>

            <!-- 表格数据 -->
            <el-table
                  size="medium"
                  ref="multipleTable"
                  :data="productData"
                  :default-sort = "{prop: 'date', order: 'descending'}"
                  tooltip-effect="dark"
                  height="86%"
                  style="width: 100%;"
                  @selection-change="handleSelectionChange">
            <el-table-column
                  type="selection"
                  width="30">
            </el-table-column>
            <el-table-column
                  type="expand"
                  width="30">
            </el-table-column>
            <el-table-column
                  label="图片"
                  width="100">
            <template slot-scope="productData">
            <div class="table-div-img">
            <img class="table-img" :src="productData.row.img">
            </div>
            </template>
            </el-table-column>
            <el-table-column
                      prop="name"
                      label="产品名称"
                      >
            </el-table-column>
            <el-table-column
                      prop="cateName"
                      label="产品分类"
                      >
            </el-table-column>
            <el-table-column
                      prop="supplierName"
                      label="供应商"
                      >
            </el-table-column>
            <!-- <el-table-column -->
                      <!-- prop="attrName" -->
                      <!-- label="规格" -->
                      <!-- width="120"> -->
            <!-- </el-table-column> -->
            <el-table-column
                  prop="date"
                  sortable
                  label="日期"
                  >
            <template slot-scope="scope">{{ scope.row.date }}</template>
            </el-table-column>
            <el-table-column
                      fixed="right"
                      label=""
                      width="100">
            <template slot-scope="scope">
            <el-dropdown trigger="click">
            <span class="el-dropdown-link">
                <i class="el-icon-more"></i>
            </span>
            <el-dropdown-menu slot="dropdown" size="medium">
            <el-dropdown-item v-on:click.native="open">修改</el-dropdown-item>
            <el-dropdown-item v-on:click.native="redirect(scope.row.id)">发布产品</el-dropdown-item>
            </el-dropdown-menu>
            </el-dropdown>
            </template>
            </el-table-column>
            </el-table>

            <div class="page-div">
                <el-pagination
                      style="float:right;margin-top:10px;"
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page="currentPage1"
                      :page-sizes="[100, 200, 300, 400]"
                      :page-size="100"
                      layout="jumper, prev, pager, next, sizes, total"
                      :total="400">
                </el-pagination>
            </div>
    </div>
    <!-- <button v-on:click="test()">Test</button> -->
</div>
</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            options: [{
                value: 'zhinan',
                label: '指南',
                children: [
                {
                    value: 'daohang',
                    label: '导航',
                    children: [{
                        value: 'cexiangdaohang',
                        label: '侧向导航'
                    }, {
                        value: 'dingbudaohang',
                        label: '顶部导航'
                    }]
                }]
            }],
            options2: [{
                value: '选项1',
                label: '商品基础信息'
            }],
            value: '',
            // 表格
            productData: [],
            multipleSelection: [],
            currentPage1: 1,
            currentPage2: 2,
            currentPage3: 3,
            currentPage4: 4,
            pageSize: 100,
        };
    },
    methods:{
        open() {
            this.$alert('这是一段内容', '标题名称', {
                confirmButtonText: '确定',
                callback: action => {
                    //this.$message({
                    //    type: 'info',
                    //    message: `action: ${ action }`
                    //});
                }
            })
        },
        test() {
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        initTable() {
            let initList = [];
            let url = "/v1/products"; 
            let _this = this;
            this.$axios.get(url).then((res) => {
                _this.totalSize = parseInt(res.data.data.page_total);
                res.data.data.list.forEach((item) => {
                    console.log(item);
                    let temp = {};
                    temp.id = item.id;
                    temp.date = item.create_time;
                    temp.name = item.name;
                    temp.cateName = item.category_name;
                    temp.supplierName = item.sid_list;
                    temp.attrName = item.attr_list;
                    temp.img = item.img_list[0];
                    _this.productData.push(temp);
                });
                console.log(_this.productData);
            }).catch((err) => {
                console.log(err);
            })
        },
        redirect(id) {
            this.$router.push('/product/release/add?pid=' + id);
        }
    },
    created() {
        this.initTable();
    }
}
</script>

<style>
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */
.main-container{
    background-color:white;
    width:100%;
    height:100%;
    border-top:1px solid #ccc;
}
.el-col {
    border-radius: 4px;
}
.main-content{
    padding:10px 30px 0 30px;
    height:100%;
}
.grid-content {
    border-radius: 4px;
    min-height: 36px;
}
.for-holder-color .el-input__inner::-webkit-input-placeholder {
    color: black;
}
.for-holder-color .el-input__inner:-moz-placeholder {
    color: black;
}
.for-holder-color .el-input__inner:-ms-input-placeholder {
    color: black;
}
/* 设置table trhover 字体 */
tr:hover{
    color:#13c19f;
}
.input-search{
    width:260px;float:left;margin:11px;
}
.high-search{
    float:left;margin-top:10px;font-size:14px;line-height:32px;color:#aaa;
}
.select-c{
    color:black;width:120px;float:left;margin:10px;
}
.btn-add{
    float:right;margin:5px;background-color:#13c19f;color:white;border-color:#13c19f;
}
.page-div{
    width:100%;height:10%;
}
.select-out{
    float:right;margin:5px;width:80px;
}
.p-span{
    text-align:left;float:left;font-size:14px;line-height:20px;color:#96a6b6;
}
.p-span-active{
    color:black;
}
.table-img{
    width:100%;height:100%;
}
.table-div-img{
    width:50px;height:50px;border:1px solid #ccc;
}
</style>



